.header {
  height: 250px;
  width: 100%;
  background: rgb(255,235,0);
  background: linear-gradient(165deg, rgba(255,235,0,1) 0%, rgba(251,135,10,1) 99%);
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.logo {
  display: flex;
  align-items: center;
  margin-left: 5%;
  margin-top: 20px;

  img {
    width: 52px;
    height: 45px;
    margin-right: 10px;
  }
}

.contacts {
  margin-top: 20px;
  z-index: 1;
  border: 2px solid black;
  padding: 12px;
  border-radius: 20px;
  background: white;

  .item {
    display: flex;
    margin: 15px 0;

    img {
      width: 24px;
      height: 24px;
      margin-right: 5px;
    }
  }
}