.wrapper {
  height: 300px;
  position: relative;
  padding-top: 170px;
  display: flex;
  flex-direction: column;

  h1 {
    text-align: center;
    margin: 0;
    font-size: 60px;
    z-index: 1;
  }
}

.vector {
  position: absolute;
  top: -300px;
}

.car {
  top: -170px;
  left: 33%;
  display: block;
  position: absolute;
}

.cone {
  height: 244px;
  width: 200px;
  position: absolute;
  top: 5px;
  right: 12%;
  z-index: 1;
}

.cone2 {
  height: 244px;
  width: 200px;
  position: absolute;
  bottom: 36px;
  z-index: 1;
}

.vector2 {
  position: absolute;
  top: 145px;
}

.contentWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  background: rgb(255,235,0);
  background: linear-gradient(352deg, rgba(255,235,0,1) 0%, rgba(251,135,10,1) 99%);
}

.courses {
  display: flex;
  width: 80%;
  align-self: center;
  justify-content: space-evenly;
  margin-top: 40px;
  z-index: 1;
}

.itemWrapper {
  width: 460px;
}

.item {
  align-items: center;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  padding: 20px 40px;
  box-shadow: 4px 4px 8px 0px rgb(3 207 0 / 40%);
  transition: all 0.5s ease;

  &:hover {
    box-shadow: 4px 4px 8px 5px rgb(3 207 0 / 40%);
    transform: scale(1.01);
  }

  h2 {
    margin: 20px;
    font-size: 40px;
  }

  &:first-child {
    background-repeat: no-repeat;
    background-position: center;
    background-image: linear-gradient(to right, rgba(255,255,255, 0.5) 0 100%), url("../../assets/gears.png");
    background-size: cover;
  }

  &:last-child {
    background-repeat: no-repeat;
    background-position: center;
    background-image: linear-gradient(to right, rgba(255,255,255, 0.5) 0 100%), url("../../assets/automatic.png");
    background-size: cover;
  }
}

.info {
  display: flex;
  flex-direction: column;

  p {
    font-size: 22px;
    margin: 10px;
  }
}

.btn {
  position: relative;
  width: 210px;
  padding: 26px 40px;
  text-align: center;
  font-size: 35px;
  border-radius: 12px;
  margin: 30px auto;
  background-color: #00cf00;
  font-weight: bold;
  border: 2px solid #00000059;

  span {
    border: 1px solid #00000059;
    position: absolute;
    font-size: 18px;
    top: -2px;
    right: -2px;
    background-color: orange;
    padding: 5px;
    border-radius: 8px;
    font-weight: normal;
  }
}

